}
.youtube-card img,
.youtube-player img {
  margin: auto;
  display: block;
  max-width: 100%;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  border-width: 0;
  border-style: solid;
  height: auto;
  -webkit-transition: 0.4s all;
  -moz-transition: 0.4s all;
  transition: 0.4s all;
}
.youtube-card iframe,
.youtube-player iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: transparent;
}
