.youtube-player .play {
  left: 0.5rem;
  bottom: 0.5rem;
  color: #e7e3fa;
  text-align: center;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 0.5rem;
  background-color: rgba(0, 0, 0, 0.85);
  transition: background-color 0.2s;
  border-color: white;
  border-style: solid;
  border-width: 2px;
  position: absolute;
  cursor: pointer;
}
.youtube-card .play::after,
.youtube-player .play::after {
  position: relative;
  top: 0.75rem;
  left: 2px;
  content: " ";
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 14px 0 14px 24px;
  border-color: transparent transparent transparent #fff;
}
